.StepIndicator {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%; }

.StepIndicator-step {
  color: #fff;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  font-size: 14px;
  padding-top: 15px;
  position: relative;
  text-align: center; }

.StepIndicator-step::after {
  border-radius: 50%;
  border: 3px solid #ff625f;
  content: "";
  height: 15px;
  left: calc(50% - 7.5px);
  position: absolute;
  top: calc(-7.5px + 1.5px);
  width: 15px;
  z-index: 1; }

.StepIndicator-step::before {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  content: "";
  height: 3px;
  left: calc(-50% + 15px);
  position: absolute;
  top: 0;
  width: calc(100% - 30px); }

.StepIndicator-step:first-child::before {
  display: none; }

.StepIndicator-step--complete::after {
  background-color: #ff625f;
  border-color: #ff625f; }

.StepIndicator-step--complete::before {
  background-color: #ff625f; }

