.hero-section {
  text-align: center;
  min-height: 100vh;
  padding: 1em 3em; }

.hero-section.-height-fit {
  min-height: fit-content; }

.hero-section.-mapped {
  padding: 0; }

.hero-section.-purple {
  background: radial-gradient(circle, #3241A6 0, #202A6B 120%);
  color: white; }

.hero-section.-centered {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

.hero-section.-centered-horizontal {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap; }

.hero-section > .flowgrid {
  display: flex;
  flex-flow: row wrap;
  padding-top: 20px;
  align-items: center;
  justify-content: center; }

.hero-section > .logo {
  max-width: 100%;
  min-width: 200px; }
  .hero-section > .logo.-small {
    width: 25%; }
  .hero-section > .logo.-medium {
    width: 40%; }

.hero-section > .title {
  color: #3d3d3d;
  margin: 20px 0 40px 0; }

.hero-section > .manila {
  margin: 0; }

.hero-section > .info {
  margin: 2em 0; }
  .hero-section > .info > .link {
    color: #A69732;
    text-decoration: none; }
    .hero-section > .info > .link:visited {
      color: #A69732; }

.hero-section > .event > p {
  text-align: justify; }

.hero-section > .registration {
  margin: 3.5em 0; }

.hero-section > .when {
  font-size: 1.25em; }

.hero-section > .venue {
  margin: 0;
  padding: 0;
  font-size: 1.25em; }

